@import url("https://fonts.googleapis.com/css?family=Acme&display=swap");

.maxContent {
  flex-wrap: wrap;
  width: inherit;
  font-family: sans-serif;
  letter-spacing: 0.05em;
}
div.maxAppContainer {
  width: 100vw !important;
  height: 100%;
  display: flex !important;
  background-color: #fef7ec;
  justify-content: space-between;
  /* align-items: stretch; */
}
.navItem {
  font-size: 2.5vw;
  cursor: pointer;
}
.navItem a {
  text-decoration: none;
  color: #1d190e;
  position: relative;
}
.navItem a::before {
  content: " ";
  display: block;
  position: absolute;
  height: 3px;
  background-color: rgba(186, 95, 35, 0.8);
  /* border-bottom: solid 3px rgba(186, 95, 35, 0.8); */
  bottom: 0px;
  width: 100%;
  transform: scaleX(0);
  left: 0;
  margin: 0 auto;
  transform-origin: bottom left;
  transition-timing-function: ease-in-out;
  transition: all 0.3s;
  z-index: -1;
}
.navItem a:hover::before {
  transform: scaleX(1);
  background-color: #1d190e;
  /* border-bottom: solid 3px #1d190e; */
  /* width: 100%; */
  left: 0%;
  bottom: 0px;
  transform-origin: bottom left;
}
.maxSpacer {
  height: 70vh;
}
.navItemsContainer {
  width: inherit;
  margin-top: 0 !important;
  top: 1.5vw;
  position: -webkit-sticky;
  position: sticky;
  padding-inline-start: 2vw;
}
.maxHeader {
  font-family: sans-serif;
  color: #1d190e;
  font-size: 15vw;
  word-break: break-all;
  text-align: left;
  line-height: 85%;
  margin-bottom: 10vw;
  letter-spacing: 0.04em;
}
.maxNav {
  color: #1d190e;
  /* width: 20vw; */
  display: flex;
  flex-direction: column;
  flex: 1;
}
.maxMain {
  color: #1d190e;
  padding-left: 3vw;
  padding-right: 5vw;
  padding-bottom: 10vw;
  padding-top: 3vw;
  flex: 4 !important;
  background-color: #fef7ec;
}

li {
  list-style-type: none;
}
.beef {
  background-color: yellow;
}
.rawr {
  display: none;
  padding: 20px;
  height: 33px;
  width: 41px;
}
@media screen and (max-width: 700px) {
  .maxNav {
    display: none;
  }
  .maxMain {
    padding-top: 6vw;
    padding-left: 6vw;
    flex: unset;
  }
  .maxHeader {
    font-size: 20vw;
    padding-bottom: 20vw;
  }
}
